
.tool-bar{height:28px;min-height:28px
}
.tool-bar .navbar-brand{height:28px;padding:4px 15px
}
.tool-bar .navbar-btn,.tool-bar .navbar-btn-o{margin-top:3px;margin-bottom:1px;font-size:12px;color:#c9c9c9
}
.tool-bar .navbar-btn{padding:3px 12px;margin-right:1px;border-radius:0;border-width:0
}
.tool-bar .navbar-btn-o{padding:1px 12px;border-width:2px;border-radius:2px
}
.tool-bar #btn-save::after{height:1px
}
.tool-bar #btn-ff,.tool-bar #btn-compile{margin-right:10px;border-top-right-radius:2px;border-bottom-right-radius:2px
}
.tool-bar #btn-next,.tool-bar #btn-tidy{border-top-left-radius:2px;border-bottom-left-radius:2px
}


