
#main{position:relative;height:100%;padding-top:28px;padding-bottom:20px
}
.layout .active-box .box-header{background:linear-gradient(#434f60, #414c5b)
}
.layout .active-box .box-header .box-title{color:#bbbbbb
}
.layout .active-box .box-body li.active{background-color:#2f65ca;color:#FFFFFF
}
.layout .upper-box{zoom:1;overflow:hidden
}
.layout .upper-box .center-box{float:left;width:100%;margin:0
}
.layout .upper-box .center-box .center-box-inner{height:100%
}
.layout .upper-box .left-box{float:left;margin-left:-100%;position:relative;width:200px
}
.layout .upper-box .right-box{float:left;position:relative;width:200px;margin-left:-200px
}


