
@import "style/bootstrap.extract.css";
@import "style/glyphicon.extract.css";
@import "style/style.css";
#app{width:100%;height:100%
}
#app #about-mask .mask-backdrop{position:fixed;width:100%;height:100%;left:0;top:0;background:rgba(0,0,0,0.4);z-index:1050
}
#app #about-mask #about-dialog{position:fixed;width:600px;height:auto;top:50%;left:50%;transform:translate3d(-50%, -50%, 0);margin:0 auto;max-width:80%;background:#3c3f41;color:#bbb;border-radius:3px;font-size:13px;overflow:hidden;box-shadow:0 0 10px 0 rgba(0,0,0,0.3);z-index:5000
}
#app #about-mask #about-dialog .dialog-banner{width:100%;height:200px;background:#FEDD31;color:#000000;text-align:center;padding:40px
}
#app #footer .footer-text.footer-btn{padding:5px 1em;margin:0 0 0 1px
}
#app #footer .footer-text.footer-btn.active{background-color:#2c2e2f
}
#app #footer .footer-text.footer-btn:not(.active):hover{background-color:#353739
}


